import React, { Component } from 'react'
import { Link, NavLink } from 'react-router-dom';

export default class product_list extends Component {
    constructor(props) {
        super(props);
        this.state = {
            product: [
                { id: "1", link: "#Tab_1", title: "ALL", img:"assets/img/tea01.jpg" }, 
                { id: "2", link: "#Tab_2", title: "TEA & COFFEE", img:"assets/img/tea01.jpg" }, 
                { id: "3", link: "#Tab_3", title: "SALADS", img:"assets/img/salads01.jpg" },
                { id: "4", link: "#Tab_4", title: "APPETIZERS", img:"assets/img/appertizer01.jpg" },
                { id: "5", link: "#Tab_5", title: "MOCKTAILS", img:"assets/img/mocktails01.jpg" },
            ],
        }
    }
    render() {
        const Products = this.state.product.map((val, index) => 
        <li key={index} className="nav-item active">
            <a href={val.link} data-toggle="tab">
                <div className="tab_item">
                    <img src={val.img} alt="" />
                </div>
                <h3 className="tab_title">{val.title}</h3>
            </a>
        </li>);
        return (
            <>
                <ul className="nav nav-tabs" id="myTab" role="tablist">
                    {Products}
                </ul>
            </>
        )
    }
}

