import React, { Component } from 'react'
import Banner from '../sub_components/banner'
import Products from '../sub_components/products'

export default class Home extends Component {


    render() {
        return (
            <>
  <div>
      <section className="banner">
        <img src="assets/img/black rectangle.svg" alt="" />
      </section>

      <section className="intro text-center">
        <div className="container">
          <p>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
            eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim
            ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut
            aliquip ex ea commodo consequat.
          </p>
          <div className="row m-4">
            <a href="">Learn more</a>
            <a href="">Buy</a>
          </div>
          <div className="row m-2">
            <button
              className="btn custom-btn review-btn"
              data-toggle="modal"
              data-target="#videoModal"
            >
              REVIEW LIVE
            </button>
          </div>
        </div>
      </section>

      <section>
      <div className="container-fluid">
        <div className="row">
          <div className="col-md-6 p-0">
            <div className="img-wrapper">
              <img src="assets/img/img1.svg" alt="" />
            </div>
          </div>
          <div className="col-md-6 p-0">
            <div className="img-wrapper">
              <img src="assets/img/img2.svg" alt="" />
            </div>
          </div>
          </div>
        </div>
      </section>
    </div>
             </>
        )
    }
}
