import React from "react";
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';

const Product = () => {
  const options = {
    responsiveClass: true,
    nav: false,
    dots: false,
    autoplay: false,
    responsive: {
        0: {
            items: 1,
        },
    },
};  
const options2 = {
  margin:15,
  responsiveClass: true,
  nav: false,
  dots: false,
  center: true,
  autoplay: false,
  responsive: {
      0: {
          items: 3,
      },
  },
};  


  return (
    <div>
      <div className="product-header-top">
        <h2>Galaxy Buds Pro</h2>
        <h5>From $12.43 / month for 20 months</h5>
        <button className="btn custom-btn blue">Add to Cart</button>
      </div>
      <div className="product-header-bottom">
        <ul className="ph-list">
          <li>
            <a href="">Characterstics</a>
          </li>
          <li>
            <a href="">Support</a>
          </li>
          <li>
            <a href="">Offer</a>
          </li>
          <li>
            <a href="">Response</a>
          </li>
        </ul>
      </div>
      <div className="product-title-bar">
        <h5>Galaxy Buds Pro with Wireless Charger</h5>
      </div>
      <div className="product-details">
        <div className="container">
          <div className="row">
            <div className="col-md-6">
              <div className="product-slider">
              <OwlCarousel className='owl-theme' {...options}>
                  <div className="item">
                    <div className="product-item">
                      <img src="assets/img/buds-1.png" alt="" />
                    </div>
                  </div>
                  <div className="item">
                    <div className="product-item">
                      <img src="assets/img/buds-1.png" alt="" />
                    </div>
                  </div>
                  <div className="item">
                    <div className="product-item">
                      <img src="assets/img/buds-1.png" alt="" />
                    </div>
                  </div>
             </OwlCarousel>
              </div>
            </div>
            <div className="col-md-6">
              <div className="product-details">
                <h2>Galaxy Buds Pro</h2>
                <div className="review-title">
                  <img src="assets/img/Star.png" alt="" />
                  <h3>View reviews</h3>
                </div>
                <h3 className="mb-3">Select Color</h3>
                <div className="color-slider">
                  <OwlCarousel className='owl-theme' {...options2}>
                    <div className="item">
                      <div className="color-item">
                        <span className="color black"></span>
                        <p>Black</p>
                      </div>
                    </div>
                    <div className="item">
                      <div className="color-item">
                        <span className="color purple"></span>
                        <p>Black</p>
                      </div>
                    </div>
                    <div className="item">
                      <div className="color-item">
                        <span className="color grey"></span>
                        <p>Black</p>
                      </div>
                    </div>
                  </OwlCarousel>
                </div>
                <div className="pd-card">
                  <div className="pdc-header">
                    <h5>Galaxy Buds Pro</h5>
                    <p>Vioket</p>
                  </div>
                  <div className="pdc-content">
                    <h4>From € 12.43 / month for 20 months or € 229.00</h4>
                    <button className="btn custom-btn blue">Add to Cart</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Product;
